  .header-home{
    border-bottom: 1px solid lightgrey;
    padding: 10px;
    text-align: center;
    background-color : rgba(255,255,255, 0.5);
    background : url('https://art.websimple.ch/wp-content/uploads/2020/12/alice-donovan-rouse-yu68fUQDvOI-unsplash-scaled.jpg');
    height : 400px;
    width : 100%;
    display : table;
    position : fixed;
  }

  h1{
    display : table-cell;
    vertical-align : middle;
    text-align : center;
  }

  h6 img {
    width: 24px;
  }
body {
  font-family: Rubik, "Avenir Next", sans-serif;
  color: rgb(87, 87, 87);
  background-color: white;
}
h1, h2, h3 {
  color: black;
}

a {
  color: #65acef;
}

nav ul {
  list-style: none;
  display: flex;
  background-color: black;
  margin-bottom: 20px;
  position : fixed;
  z-index: 10;
  width : 100%;
}

nav ul li {
  padding: 20px;
}

nav ul li a {
  color: white;
  text-decoration: none;
}
